*, :before, :after {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

body {
  text-transform: uppercase;
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.header__img {
  width: 40px;
  height: 40px;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__item {
  text-align: center;
  justify-content: center;
  margin: 0 10px;
  list-style: none;
  display: flex;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: inherit;
  align-self: center;
  height: 60px;
  padding-top: 22.5px;
  text-decoration: none;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
